.AddProgram {
    background-color: #fff;
    box-shadow: 1px 1px 2px rgba(#ddd, 0.8s);
    margin: auto;
  
    display: grid;
    grid-template-rows: repeat(6, max-content);
    grid-row-gap: 2rem;
    padding: 1rem;
  
    h1 {
      color: var(--color-primary);
    }
  }
  
  .form-group {
    label {
      font-size: 1rem;
      font-weight: 800;
  
      display: block;
    }
  
    select {
      background-color: rgba(#ccc, 0.4);
      border: none;
      padding: 0.8rem;
      border-radius: 0.4rem;
    }
  
    &__input {
      background-color: rgba(#ccc, 0.4);
      border: none;
      padding: 0.8rem;
      border-radius: 0.4rem;
  
      &--fullwidth {
        width: 100%;
      }
    }
  
    textarea {
      width: 100%;
      height: 150px;
      background-color: rgba(#ccc, 0.4);
      border: 1px solid #ccc;
      resize: none;
      padding: 1rem 1rem;
      font-family: inherit;
    }
  }
  
  .btn {
    cursor: pointer;
    width: 100%;
  }
  
  .btn-submit {
    padding: 0.7rem;
    border-radius: 8px;
    background-color: #fff;
    border: 1px solid black;
    font-weight: 800;
    color: var(--color-primary);
    transition: all ease-in 0.3s;
  
    &:hover {
      background-color: var(--color-primary);
      color: #fff;
    }
  
    &:focus {
      transform: scale(0.9);
      -webkit-transform: scale(0.9);
    }
  }

  #add_program {
    color: #111;
    font-weight: 800;
    text-decoration: none;
    background-color: #fff;
    box-shadow: 1px 1px 5px rgba(#ddd, 0.9);

    justify-self: end;
    border-radius: 2rem;

    display: grid;
    grid-template-columns: 2rem max-content 0.4rem;
    grid-column-gap: 0.4rem;
    align-items: center;
  }
  .IconAdd {
    color: #fff;
    background-color: var(--color-primary);
    width: 2rem;
    height: 2rem;
    border-radius: 2rem;
  }
  